import React from 'react'
const ShowReview = ({ review }) => {

    const margin = {
        margin: 20
    }
return(
    <div style={margin}>
    <div>
     Rating: {review.rating}
    </div>
    <div>
    Review: {review.review}
    </div>
    <div>
    {review.user} {review.date}
    </div>

    </div>)
}
export default ShowReview
