import React, { useState } from 'react'
import NumericInput from 'react-numeric-input';


const Review = ({ movie, getReview }) =>
{
    const [rating,setRating] = useState(5)
    const [writeReview,setWriteReview] = useState("")
    


const reviewAdder = (event) =>
{
 event.preventDefault()
// const review ={...movie,rating,review:writeReview}

const review = {Title:movie.Title,Year:movie.Year,Poster:movie.Poster,imdb:movie.imdbRating,reviews:[]}

console.log(review.imdb)
review.reviews.push({rating,review:writeReview})

console.log(review)
 getReview(review)

 setRating('')
 setWriteReview("")

}

if(movie === null)
{
return null
}
else{
    return(
  <div>

    {movie.Title} {movie.Year} 
    <p>
    <img src={movie.Poster} alt="Poster"/>
     </p>
    IMDB-rating:{movie.imdbRating}
     <form onSubmit={reviewAdder} >
      <h2>Review movie</h2>
    Rating <NumericInput required  min={0} max={10} step={0.1} value={rating} name="rating" onChange={setRating}/>

 
     <p> Review</p><p><textarea value={writeReview} name="review"  rows={8} cols={50} 
         onChange={({ target }) =>setWriteReview(target.value)}/>
  </p>
  <button type="submit">Rate movie</button>
</form>


  </div>
    )
}
}

export default Review