import React, { useState, useEffect } from 'react'
import {
  BrowserRouter as Router,
  Switch, Route, Link
} from "react-router-dom"

import ListOfMovies from './components/ListOfMovies'
import Movie from './components/Movie'
import Review from './components/Review'
import Login from './components/Login'
import Information from './components/Information'
import SearchMovieToReview from './components/SearchMovieToReview'
import movieService from './services/movies'
import loginService from './services/login'

const App = () => {
  const [movies, setMovies] = useState([])
 // const [search, setsearch] = useState('')
  const [movieToReview,setMovieToReview] = useState(null)
 // const [username, setUsername] = useState('') 
 // const [password, setPassword] = useState('') 
  const [user, setUser] = useState(null)
  const [errorMessge,setErrorMessage] = useState(null)
  const [usersMovies, setUsersMovies] = useState([])
  

 const sortByTitle = ((a,b) =>{
    if(a.Title > b.Title)
    {
      return 1
    }
    else if (b.Title  > a.Title)
    {
      return -1
    }
    else
    {
      return 0
    }
  })

  useEffect(() => {
    movieService.getAll().then(movies =>
      {

       movies.sort(sortByTitle); 
      setMovies( movies )
    
      }
    )  
  }, [])


  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogAppUser')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      setUser(user)
    movieService.setToken(user.token)

    if(user)
    {
      movieService.getUsersReviews(user.id).then((data)=>
      {
        data.movies.sort(sortByTitle)
        setUsersMovies(data.movies)
      })
    }
    }
  }, [])

 /* const handleLogin = (event)=>{
    event.preventDefault()
    loginService.login({username,password}).then((user)=>{
      console.log(user)
      window.localStorage.setItem(
        'loggedBlogAppUser', JSON.stringify(user)
      ) 
      movieService.setToken(user.token)
      setUser(user)
      setUsername("")
      setPassword("")
    }
    ).catch((error)=>{ 
    setErrorMessage(
      `${error.response.data.error}`
    )
    setTimeout(() => {
      setErrorMessage(null)
    }, 2000)
  })
} 
*/
const logout = ()=>{

  window.localStorage.removeItem('loggedBlogAppUser')
  setUser(null)
  movieService.setToken(null);
  setUsersMovies([])

}
  const searchFromAPI = (search)=>    //Haetaan eolkuvan tiedot omdbapi:sta
  {
  //  event.preventDefault()
    
    movieService.searchMovie(search).then((response)=>
    {
      
     if (response.data.Response!=="False")
     {
     setMovieToReview(response.data)
     }
     else
    {
      setMovieToReview(null)
    }
   //  setsearch("")
    }).catch((error)=>
    {
    console.log(error)
    })
  }
  const makeReview =(review) =>   //K채sitell채채n Review komponentilla tehty arvostelu
  {
   // event.preventDefault()
   // const review ={...movieToReview,rating}
  
 //  review = {...review,user:user.username}
    review.reviews[0].user = user.username

    console.log(review.imdb)
    movieService.create(review).then((newReview)=>
    {
      const index = movies.findIndex((movie)=>
      {
       return movie.Title ===review.Title
      })
      
      if(index === -1)
      {
      
      setMovies(movies.concat(review))    //Kaataa ohjelman jos tekee toisen arvoselun samalla istunnolla
      }
      else
      {
        let newArray = [...movies]
        //newArray[index] = {...newArray[index].reviews.push(newReview)}

       
        newArray[index].reviews.push(newReview)
       
        setMovies(newArray)
       
      }

      const indexOwnReviews = usersMovies.findIndex((movie)=>
      {
       return movie.Title ===review.Title
      })
      
      if(indexOwnReviews === -1)
      {
      
      setUsersMovies(usersMovies.concat(review))    //Kaataa ohjelman jos tekee toisen arvoselun samalla istunnolla
      }
      else
      {
        let newArray = [...usersMovies]
        //newArray[index] = {...newArray[index].reviews.push(newReview)}

       
        newArray[indexOwnReviews].reviews.push(newReview)
       
        setUsersMovies(newArray)
       
      }



      setMovieToReview(null)
    })
 
  }

  const login =(username,password) =>
  {

    loginService.login({username,password}).then((user)=>{
     // console.log(user)
      window.localStorage.setItem(
        'loggedBlogAppUser', JSON.stringify(user)
      ) 
      movieService.setToken(user.token)
    
      movieService.getUsersReviews(user.id).then((data)=>  
      {
        setUsersMovies(data.movies)
      })
      setUser(user)
    }).catch(()=>console.log("Login failed"))
  }
  const padding = {
    padding: 5
  }
const margin ={
  margin:20
}

return(
<div style = {margin}>
  <Router>
     
      <div>
        <Link style={padding} to="/">home</Link>
        <Link style={padding} to="/reviews">all reviews</Link>
        <Link style={padding} to="/your_reviews">movies reviewed by you</Link>
        <Link style={padding} to="/make_review">make review</Link>
        {user
      ? <em>{user.username} logged in <button onClick={logout}> Logout</button></em>
      : <span>You are not logged in</span>
     // : <Link style={padding} to="/login">login</Link>
    }
      </div>

      <Switch>
        <Route path="/reviews">
          <ListOfMovies  movies={movies} />
        </Route>
        <Route path="/your_reviews">
          {user?
          <ListOfMovies movies={usersMovies} />:
          <Login getLoginInformation={login}/>}
        </Route>
        <Route path="/make_review">
          {user? <div >
        <SearchMovieToReview getSearchedTitle={searchFromAPI}/>
        <Review movie={movieToReview} getReview={makeReview} />
        </div>
        :<Login getLoginInformation={login}/>}
        </Route>
        <Route path="/login">
        <Login getLoginInformation={login}/>
        </Route>
       < Route path="/">
       <Information/>
        </Route>
      </Switch>

      <div style={{clear:'both'}}>
        <i>Teemu Hallinen, Ohjelmistosuunnittelu t채ydennyskoulutus 2019-2020</i>
      </div>
    </Router>
    </div>
)
  
}
export default App